.header-title {
    text-align: center;

    margin-top: 194px;

    h2 {
        font-family: 'Playfair Display', serif;
        font-size: 70px;
        line-height: 100px;
        font-weight: 400;

        margin: 0;
    }

    p {
        font-size: 16px;
        font-family: 'Lato', sans-serif;
        color: #999;
        line-height: 30px;

        margin: 0;
        margin-top: 15px;
    }
}


.header-content {
    margin-top: 205px;
    display: flex;

    h3 {
        font-family: 'Playfair Display', serif;
        text-transform: capitalize;
        font-size: 35px;
        margin: 0; margin-top: 24px;
    }
}

.header-content-reviews {
    width: 410px;
}

.reviews-item {
    position: relative;

    margin: 0; margin-top: 42px;
    width: 255px;

    font-size: 16px;
    letter-spacing: 0.2px;
    line-height: 31px;
    color: #999;

    padding-bottom: 15px;
    border-bottom: 1px solid #eee;

    cite {
        display: block;
        margin-bottom: 5px;
        
        color: #000;
        font-style: normal;
        font-weight: bold;
    
    }  

    &::before {
        content: "";
        position: absolute;
        top: 0; left: -15px;
        display: block;

        width: 36px;
        height: 29px;
        z-index: -1;

        background-image: url('../../img/icons/quote.png');


    }
}

.header-content-video {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 1030px
}

.video-list {
    width: 280px;

    ul {
        margin-top: 28px;
        list-style: none;
        padding: 0;
    }
}

.video-list-item {
    position: relative;
    margin-bottom: 35px;
}

.video-list-item .play-icon {
    position: absolute;
    left: -10px; bottom: -15px;
}

.main-video {
    position: relative;
}

.play-icon {
    position: relative;
    width: 56px;
    height: 56px;

    background-color: rgba($color: #fff, $alpha: 0.8);

    border-radius: 50px;
    border: none;

    cursor: pointer;


    &:before {
        content: "";
        display: block;

        position: absolute;
        left: 50%; top:50%;
        margin-left: -4px;
        margin-top: -5.5px;

        width: 0; 
        height: 0;

        border-top: 5.5px solid transparent;
        border-left: 8px solid #000;
        border-bottom: 5.5px solid transparent;

    }
}

.play-icon:hover {
    background-color: #fff;

    transition: all 0.1s ease;
}


.main-video .play-icon {
    position: absolute;
    left: 25px; bottom: 32px;
}